.switch {
	position: relative;
	display: inline-block;
	width: 60px;
	height: 34px;
  }
  
  .switch input { 
	opacity: 0;
	width: 0;
	height: 0;
  }
  
  .slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #ccc;
	transition: .4s;
  }
  
  .slider:before {
	position: absolute;
	content: "";
	height: 26px;
	width: 26px;
	left: 4px;
	bottom: 4px;
	background-color: white;
	transition: .4s;
  }

  .slider:hover{
	box-shadow: 0 8px 16px 0 rgba(255, 255, 255, 0.508);
	transition: .4s;
  }  
  
  input:checked + .slider {
	background-color: #C62032;
  }
  
  input:focus + .slider {
	box-shadow: 0 0 1px #C62032;
  }
  
  input:checked + .slider:before {
	-webkit-transform: translateX(26px);
	transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
	border-radius: 34px;
  }
  
  .slider.round:before {
	border-radius: 50%;
  }
