  .popup-overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5); /* semi-transparent black background */
	z-index: 999; /* make sure the overlay is on top of other elements */
	display: flex;
	justify-content: center;
	align-items: center;
  }
  
  .popup-content {
	background: white;
	padding: 1rem;
	border-radius: 0.25rem;
	box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2); /* add a subtle drop shadow */
	max-width: 30rem; /* set a max width for the popup box */
	text-align: center;
  }
  

